.has-error {
  border-color: red;
  background-color: #fff0e8;
}

.has-error:focus {
  color: red;
  border-color: red;
}

.card-body {
  margin-top: -15px;
}

.card-panel {
  background-color: transparent;
}

.small .btn-sm {
  padding: .2rem .4rem;
  font-size: .75rem;
  line-height: 1.25;
  border-radius: .25rem;
}

.form-check-right {
  margin-right: 1.1rem;
}

.form-check-right input {
  margin-left: 0.5rem;
}

.field-required {
  font-style: italic;
  color: red;
  font-size: smaller;
}
.mobileContent {
  border: 8px solid greenyellow;
  border-radius: 0.6rem;
  padding: 0;
}

.mobileFooter {
  padding: 0;
  background-color: rgba(173, 255, 47, 0.75);
}

.mobileFooter img {
  max-height: 4.6rem;
  margin-top: 0.25rem;
}

.text-charcoal {
  color: #36454F;
}

.text-charcoal a {
  color: #36454F;
}

.text-tomato {
  background-color: tomato !important;
  color: white !important;
}

.text-black {
  color: black !important;
}

.btn-transparent {
  font-weight: 400;
  background-color: transparent;
  border-color: transparent;
}

.text-counter-zero {
  color: white;
  background-color: grey;
}

/**
 * ReSass.
 *
 * MIXIN: Responsive Media Queries.
 *
 * Creates responsive media queries for seven different screen sizes.
 * These are based on min-width which means if x is the size then your
 * CSS will affect any device with screen width x and above.
 *
 * USAGE:
 *	@include r(240)  { }
 *	@include r(320)  { }
 *	@include r(480)  { }
 *	@include r(768)  { }
 *	@include r(1024) { }
 *	@include r(1140) { }
 *	@include r(1280) { }
 *  @include r(1500) { }
 *
 * CSS content goes inside {} brackets. These mixins should be used inside
 * a class definition. For example:
 *
 * The following CSS will hide the .header on screen width 320px and above.
 * .header {
 * 	  @include r(320)  { display: none; }
 * }
 *
 * @author  Ahmad Awais (https://github.com/ahmadawais)
 * @link    https://github.com/ahmadawais/ReSass.git
 * @version 1.0.0
 */

@media (min-width: 240px) {
  html {
    font-size: 0.4rem;
  }
}

@media (min-width: 320px) {
  html {
    font-size: 0.5rem;
  }
}

@media (min-width: 480px) {
  html {
    font-size: 0.6rem;
  }
}

@media (min-width: 600px) {
  html {
    font-size: 0.75rem;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 0.8rem;
  }
}

@media (min-width: 1024px) {
  html {
    font-size: 1.0rem;
  }
}

@media (min-width: 1140px) {
  html {
    font-size: 1.0rem;
  }
}

@media (min-width: 1280px) {
  html {
    font-size: 1.2rem;
  }
}

@media (min-width: 1500px) {
  html {
    font-size: 1.2rem;
  }
}
